export default "@import '../../vars/src/colors-indigo.css';\n@import '../../themes/src/default.css';\n\n:root {\n    /* bg */\n    --progressbar-bg-color: var(--color-light-bg-primary-inverted-alpha-10);\n\n    /* positive */\n    --progressbar-positive-color: var(--color-light-graphic-positive);\n\n    /* negative */\n    --progressbar-negative-color: var(--color-light-graphic-negative);\n\n    /* attention */\n    --progressbar-attention-color: var(--color-light-graphic-attention);\n\n    /* link */\n    --progressbar-link-color: var(--color-light-graphic-link);\n\n    /* tertiary */\n    --progressbar-tertiary-color: var(--color-light-graphic-tertiary);\n\n    /* secondary */\n    --progressbar-secondary-color: var(--color-light-graphic-secondary);\n\n    /* primary */\n    --progressbar-primary-color: var(--color-light-graphic-primary);\n\n    /* accent */\n    --progressbar-accent-color: var(--color-light-graphic-accent);\n}\n\n.container {\n    background: var(--progressbar-bg-color);\n\n    display: flex;\n    overflow: hidden;\n\n    &.s {\n        border-radius: var(--border-radius-s);\n        height: 4px;\n    }\n\n    &.m {\n        border-radius: var(--border-radius-m);\n        height: 8px;\n    }\n}\n\n.filled {\n    border-radius: inherit;\n    width: 100%;\n    transition: transform 0.4s ease;\n\n    &.positive {\n        background: var(--progressbar-positive-color);\n    }\n\n    &.negative {\n        background: var(--progressbar-negative-color);\n    }\n\n    &.attention {\n        background: var(--progressbar-attention-color);\n    }\n\n    &.link {\n        background: var(--progressbar-link-color);\n    }\n\n    &.tertiary {\n        background: var(--progressbar-tertiary-color);\n    }\n\n    &.secondary {\n        background: var(--progressbar-secondary-color);\n    }\n\n    &.primary {\n        background: var(--progressbar-primary-color);\n    }\n\n    &.accent {\n        background: var(--progressbar-accent-color);\n    }\n}\n";